



























import { Component, Vue } from "vue-property-decorator";
import UseIcon from "@/components/UseIcon.vue";
@Component({
  components: {
    UseIcon,
  },
})
export default class MenuNav extends Vue {
  screenWidth: number = window.innerWidth;
  isActive = -1;
  isChangeImage = -1;
  get classList() {
    return this.$store.state.webData;
  }
  get currentIndex() {
    return this.$store.state.currentIndex;
  }
  get isClose() {
    return this.$store.state.isClose;
  }
  mounted() {
    this.setScreenHeight();
  }
  setScreenHeight() {
    window.onresize = () => {
      if (window.innerWidth < 750) {
        this.isChangeImage = -1;
      } else if (window.innerWidth < 1000) {
        this.isChangeImage = 0;
        console.log(this.isChangeImage);
      } else {
        this.isChangeImage = 1;
      }
    };
  }
  active(index: number) {
    this.isActive = index;
  }
  toggleClose() {
    this.$store.commit("toggleClose");
  }
  scrollToPosition(index: number) {
    this.isActive = index;
    this.$store.commit("setCurrent", index);
    document.getElementsByClassName("roll")[index].scrollIntoView({
      behavior: "smooth",
      block: "start",
      inline: "nearest",
    });
  }
}
